<template>
    <grid-layout
        :layout.sync="layout"
        :row-height="rowHeight"
        :is-draggable="isDraggable"
        :is-resizable="isResizable"
        :vertical-compact="verticalCompact"
        :use-css-transforms="useCssTransforms"
        :col-num="colNum"
        :margin="margin">
        <grid-item v-for="(item, index) in layout" :key="item.i"
            :x="item.x"
            :y="item.y"
            :w="item.w"
            :h="item.h"
            :i="item.i"
            :item="item"
            :style="{backgroundColor: backgroundColor}"
            @resize="resize"
            @move="move"
            @resized="resized"
            @moved="moved">
            <button @click="closeWidget(index)"></button>
            <component :is="item.type" :item-info="item"></component>
        </grid-item>
    </grid-layout>
</template>

<script>
    import GridItem from './components/GridItem.vue';
    import GridLayout from './components/GridLayout.vue';

    import Widget from './components/Widget.vue';

    import { deepCopy } from './utils/util';

    export default {
        name: 'app',
        components: {
            // ResponsiveGridLayout,
            GridLayout,
            GridItem,
        },
        // 
        addWidgetType(type, widget){
            this.components[type] = {
                ...widget,
                extends: Widget
            }
        },
        props: {
            isDraggable: { // 
                type: Boolean,
                default: true
            },
            isResizable: { // 
                type: Boolean,
                default: true
            },
            rowHeight: { // 
                type: Number,
                default: 150
            },
            maxRows: { // 
                type: Number,
                default: Infinity
            },
            margin: [10, 10], // 
            verticalCompact: { // 
                type: Boolean,
                default: true
            },
            useCssTransforms: { // css transforms
                type: Boolean,
                default: true
            },
            colNum: { // 
                type: Number,
                default: 12
            },
            dragIgnoreFrom: { // 
                type: String,
                default: 'a, button'
            },
            dragAllowFrom: { 
                type: String,
                default: 'a, button'
            },
            resizeIgnoreFrom: {
                type: String,
                default: 'a, button'
            },
            backgroundColor: {
                type: String,
                default: 'rgba(255,255,255,1)'
            }
        },
        data () {
            return {
                layout: [], // 
            }
        },
        mounted: function () {
            this.index = this.layout.length;
        },
        methods: {
            setLayout(layout){
                this.layout = deepCopy(layout);
            },
            // 
            closeWidget: function(index) {
                this.layout.splice(index, 1);
            },
            addItem: function() {
                // let self = this;
                //console.log("### LENGTH: " + this.layout.length);
                let item = {"x":0,"y":0,"w":2,"h":2,"i":this.index+"", whatever: "bbb"};
                this.index++;
                this.layout.push(item);
            },
            move: function(item){
                // console.log("MOVE i=" + item.i + ", X=" + item.x + ", Y=" + item.y);
                this.$emit('move', item);
            },
            resize: function(item, newSize){
                // console.log("RESIZE i=" + item.i + ", H=" + item.h + ", W=" + item.w + ", H(px)=" + newSize.height + ", W(px)=" + newSize.width);
                this.$emit('resize', item, newSize);
            },
            moved: function(item){
                // console.log("### MOVED i=" + item.i + ", X=" + item.x + ", Y=" + item.y);
                this.$emit('moved', item);
            },
            resized: function(item, newSize){
                // console.log("### RESIZED i=" + item.i + ", H=" + item.h + ", W=" + item.w + ", H(px)=" + newSize.height + ", W(px)=" + newSize.width);
                this.$emit('resized', item, newSize);
            }
        },
    }
</script>
